<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <b-form @submit="onSubmit">
                  <h1>Login</h1>
                  <p class="text-muted">Sign In to your account</p>
                  <b-input-group class="mb-3">
                    <b-input-group-prepend><b-input-group-text><i class="icon-user"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="text" v-model="username" class="form-control" placeholder="Username" autocomplete="username email" />
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend><b-input-group-text><i class="icon-lock"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="password" v-model="password" class="form-control" placeholder="Password" autocomplete="current-password" />
                  </b-input-group>
                  <b-row>
                      <b-alert v-if="error" show variant="danger">{{error}}</b-alert>
                    <b-col cols="6">
                    </b-col>
                    <b-col cols="6" class="text-right">
                      <b-button type="submit" variant="primary" class="px-4">Login</b-button>
                      <!-- <b-button variant="link" class="px-0">Forgot password?</b-button> -->
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
            <b-card no-body class="text-white bg-primary py-5 d-md-down-none" style="width:34%">
              <b-card-body class="text-center">
                <div>
                  <h2>Sign up</h2>
                  <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                  <!-- <b-button variant="primary" class="active mt-3">Register Now!</b-button> -->
                </div>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import firebase from "firebase/app";

import 'firebase/auth'

export default {
  name: "Login",
  data() {
    return {
      username: "",
      password: "",
      error: ''
    };
  },
  methods: {
    onSubmit() {
      const that =  this;

      firebase
        .auth()
        .signInWithEmailAndPassword(this.username, this.password)
        .then(function(user) {
          const dataSave = user.user.toJSON();
          that.error = '';
           user.user.getIdToken().then(res=>{
            dataSave.token = res;
            localStorage.setItem('token', JSON.stringify(dataSave));
            setTimeout(() => {
              that.$router.push({path: '/'})
            }, 100);
          });
        })
        .catch(function(error) {
          console.log(error);
          that.error = error.message

        });
    }
  },
  created() {
    
  },
};
</script>
